.academicContainer {
	margin: 1rem 2rem;
	font-size: 0.8rem;
	background-color: white;
	padding: 2em 0;
}

.filterStudentForm {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	padding: 0.5rem;
}

.transcriptTable {
	margin-left: auto;
	margin-right: auto;
	width: 100%;
	text-align: left;
	border-spacing: 30px;
}

.filterStudentBtn {
	align-self: center;
	padding: 0.7em;
	padding-left: 1em;
	padding-right: 1em;
	border-radius: 10px;
	border: none;
	color: whitesmoke;
	outline: none;
	transition: 0.4s ease-in-out;
	max-width: fit-content;
	margin: 1rem;
}

.filterStudentBtn {
	margin-top: 1rem;
	width: 100%;
}

.filterStudentBtn:hover {
	box-shadow: 6px 6px 0px #c2c2c2, -3px -3px 10px #ffffff;
}

.filterStudentBtn:active {
	box-shadow: 6px 6px 0px #c2c2c2, -3px -3px 10px #ffffff;
}

.error {
	color: #d9534f;
	font-weight: bold;
	font-size: 1rem;
	padding: 5em;
	display: flex;
	align-items: center;
	justify-content: center;
}

.transcriptTable {
	width: 90%;
	border-collapse: collapse;
	font-family: Arial, sans-serif;
	margin: auto auto;
	background-color: #ffffff;
	box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.05);
}

.transcriptTable thead h2 {
	margin: 0.2em;
	padding: 0.2em;
	font-size: 1em;
	color: #ffffff;
	background-color: #ffab67;
	text-align: center;
}

.transcriptTable th,
.transcriptTable td {
	padding: 1.5em 1em;
	/* border: 1px solid #cccccc; */
	font-size: 1em;
	text-align: left;
}

.transcriptTable th[colspan='9'] {
	background-color: #ffab67;
	color: #ffffff;
	font-size: 18px;
	text-align: center;
}

.transcriptTable tbody tr:first-child td,
.transcriptTable tbody tr:nth-child(2) td {
	font-weight: bold;
}

.transcriptTable tbody tr:nth-child(3) td {
	font-weight: bold;
	background-color: #ebebeb;
	text-align: center;
	color: #333333;
}

.transcriptTable tbody tr:nth-child(odd) td {
	background-color: #f9f9f9;
}

.transcriptTable tbody tr td[colspan='1'],
.transcriptTable tbody tr td[colspan='2'],
.transcriptTable tbody tr td[colspan='6'],
.transcriptTable tbody tr td[colspan='5'] {
	text-align: left;
}

.transcriptTable tbody tr td[colspan='1'] {
	/* font-weight: bold; */
}

.transcriptTable tbody tr:hover {
	background-color: whitesmoke;
}
