.homeContainer {
	padding: 20px;
	background-color: #f4f6f9;
	font-family: Arial, sans-serif;
	margin: auto 10em;
}

.welcomeHeading {
	text-align: center;
	font-size: 2rem;
	color: #333;
	margin-bottom: 20px;
}

.featureList {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 20px;
	margin-top: 30px;
}

.featureItem {
	background-color: #fff;
	padding: 20px;
	border-radius: 8px;
	box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
	transition: transform 0.2s ease-in-out;
	cursor: pointer;
}

.featureItem h3 {
	font-size: 1.5rem;
	color: #ffab67; /* Primary theme color */
	margin-bottom: 10px;
}

.featureItem p {
	font-size: 1rem;
	color: #666;
}

.featureItem:hover {
	transform: translateY(-5px);
	box-shadow: 0 8px 12px rgba(0, 0, 0, 0.1);
}

.featureItem a {
	text-decoration: none;
	color: inherit; /* Inherit color from featureItem */
}

@media (max-width: 768px) {
	.featureList {
		grid-template-columns: 1fr;
	}
}
